<template>
  <div id="signup_wrap">
    <h1></h1>
    <el-input v-model="nickname" placeholder="NickName"></el-input>
    <el-input v-model="user_name" placeholder=""></el-input>
    <el-input v-model="password" placeholder="" type="password"></el-input>
    <el-input v-model="password_confirm" placeholder="" type="password"></el-input>
    <el-button @click="signup"></el-button>
  </div>
</template>

<script>
import * as API from "@/api/user/";
export default {
  name: "signup",
  data() {
    return {
      nickname:"",
      user_name: "",
      password: "",
      password_confirm:"",
    };
  },
  methods: {
    signup: function() {
      let _this = this;
      if (this.nickname.length < 3) {
        this.$message.error("");
        return;
      }

      if (this.user_name.length < 6) {
        this.$message.error("");
        return;
      }

      if (this.password.length < 8) {
        this.$message.error("");
        return;
      }

      if (this.password !=this.password_confirm) {
        this.$message.error("");
        return;
      }
      let obj = {
        nickname:_this.nickname,
        user_name: _this.user_name,
        password: _this.password,
        password_confirm:_this.password_confirm,
      };
      API.signUp(obj
        )
        .then(
          response => {
            console.log(response)
            _this.$message({
              message: "",
              type: "success"
            });
            _this.$router.push("/signin");
          },
          response => console.log("" + response)
        );
    }
  }
}
</script>

<style>
#signup_wrap {
  width: 300px;
  margin: 100px auto;
}

#signup_wrap h1 {
  color: #383a42;
  padding: 10px;
}

#signup_wrap div {
  padding-bottom: 20px;
}
</style>