<template>
    <el-container>
        <el-main>
            <el-col :xs="{span: 14, offset: 5}" :sm="{span: 12, offset: 2}" :md="{span: 6, offset: 2}" :lg="{span: 6, offset: 2}" :xl="{span: 6, offset: 2}">
                <el-divider content-position="left"><el-button type="text" size="medium" style="color:black"></el-button></el-divider>
                <el-input type="text" placeholder="" v-model="title" maxlength="20" show-word-limit >
                </el-input>
            </el-col>
            <el-col :xs="{span: 14, offset: 5}" :sm="{span: 12, offset: 2}" :md="{span: 10, offset: 4}" :lg="{span: 10, offset: 4}" :xl="{span: 6, offset: 2}">
                <el-divider content-position="left"><el-button type="text" size="medium" style="color:black"></el-button></el-divider>
                <el-input type="textarea" placeholder="" v-model="info" maxlength="50" show-word-limit>
                </el-input>
            </el-col>
            <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
                <mavon-editor class="editor" style="min-height: 600px" ref=md v-model="markdown" :language="language"  @imgAdd="imgAdd" @imgDel="imgDel" @save="saveDoc"></mavon-editor>
            </el-col>
            <el-col :xs="{span: 14, offset: 5}" :sm="{span: 10, offset: 2}" :md="{span: 5, offset: 2}" :lg="{span: 5, offset: 2}" :xl="{span: 5, offset: 4}">
                <el-divider content-position="left"><el-button type="text" size="medium" style="color:black"></el-button></el-divider>
                <el-input type="text" placeholder="';'" v-model="tag" maxlength="30" show-word-limit >
                </el-input>
            </el-col>
            <el-col :xs="24" :sm="{span: 10, offset: 2}" :md="{span: 10, offset: 5}" :lg="{span: 10, offset: 5}" :xl="{span: 10, offset: 5}">
                <el-button style="margin-top:30px;" round  @click="uploadDoc" :loading="uploading"></el-button>
                <el-button type="success" style="margin-top:30px;" round></el-button>
            </el-col>
        </el-main>
    </el-container>
</template>
<script>
// Local Registration
import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import * as API from "@/api/article/";
export default {
    name: 'updatearticlepage',
    components: {
        mavonEditor
    },
    data(){
        return {
            id :0,
            title: '',
            info: '',
            markdown : "",
            tag:""
        }
    },
    mounted(){
        this.id = this.$route.params.id
        this.getArticle(this.id)
    },
    methods: {
        getArticle(id) {
            let _this = this;
            API.getArticle(id
                )
                .then(
                response => {
                    console.log(response)
                    _this.title = response.data.title
                    _this.info = response.data.info
                    _this.markdown=response.data.markdown
                    _this.username=response.data.username
                },
                response => console.log("" + response)
                );
        },
        // @imgAdd event
        imgAdd(pos, $file){
            // ..
            var formdata = new FormData();
            formdata.append('image', $file);
            this.img_file[pos] = $file;
            this.$axios({
                url: '/api/v1/uploadimage',
                method: 'post',
                data: formdata,
                headers: { 'Content-Type': 'multipart/form-data'},
            }).then((url) => {
                // .url![...](0) -> ![...](url)
                /**
                * $vm mavonEditor
                * 1. : `import {mavonEditor} from ...` `$vm``mavonEditor`
                * 2. $refs: htmlref : `<mavon-editor ref=md ></mavon-editor>`$vm` `this.$refs.md`
                */
                this.$refs.md.$img2Url(pos, url.data.msg);
            })
        },

        imgDel(pos) {
            delete this.img_file[pos];
        },

        // updateDoc(markdown, text) {
        //     console.log("markdown: " + markdown);
        //     console.log("html:" + text);
        // },

        saveDoc(markdown, text) {
            let _this = this;
            let obj = {
                id:_this.id,
                title:_this.title,
                info: _this.info,
                markdown: markdown,
            };
            console.log(text);
            API.updateArticle(_this.id,obj
                )
                .then(
                response => {
                console.log(response)
                if (response.status!==404) {
                    _this.$message({
                    message: "",
                    type: "success"
                    });
                } else{
                    _this.$message({
                    message: "",
                    type: "fail"
                    })
                }
                },
                response => console.log("" + response)
                );
        },
        
        updateDoc() {
            let _this = this;
            let obj = {
                id:_this.id,
                title:_this.title,
                info: _this.info,
                markdown :_this.markdown,
            };
            console.log(obj);
            API.updateArticle(_this.id,obj
                )
                .then(
                response => {
                console.log(response)
                if (response.status!==404) {
                    _this.$message({
                    message: "",
                    type: "success"
                    });
                } else{
                    _this.$message({
                    message: "",
                    type: "fail"
                    })
                }
                },
                response => console.log("" + response)
                );
        },
    }
}
</script>
