<template>
  <div id="signin_wrap">
    <h1></h1>
    <el-input v-model="user_name" placeholder=""></el-input>
    <el-input v-model="password" placeholder="" type="password"></el-input>
    <el-button type="primary" @click="signin"></el-button>
  </div>
</template>

<script>
import * as API from "@/api/user/";
export default {
  name: "signin",
  data() {
    return {
      user_name: "",
      password: "",
    };
  },
  methods: {
    signin: function() {
      let _this = this;
      if (this.user_name.length < 6) {
        this.$message.error("");
        return;
      }

      if (this.password.length < 8) {
        this.$message.error("");
        return;
      }
      let obj = {
        user_name: _this.user_name,
        password: _this.password
      };
      API.signIn(obj
        )
        .then(
          response => {
            console.log(response)
            _this.$message({
              message: "",
              type: "success"
            });
            delete _this.password;
            window.location.href="/"
          },
          response => console.log("" + response)
        );
      }
    }
  }
</script>

<style>
#signin_wrap {
  width: 300px;
  margin: 100px auto;
}

#signin_wrap h1 {
  color: #383a42;
  padding: 10px;
}

#signin_wrap div {
  padding-bottom: 20px;
}
</style>